import React, {Component} from 'react';
import Card from '../card/card'
import './Cards.scss';

class AppCards extends Component {
    render(){
        const { shoppingCartData, addItems} = this.props
        const card = shoppingCartData.map((item, index) => {
            return <div key={index}> <Card eachShoppingCartData = {item} addItems={addItems} index={index}/></div>
        })
        return(
            <div className="App-cards">
                {card}
            </div>
         );

    }
}

export default AppCards